.welcome {
    align-content: center;
    padding: 0;
	background-position: center center;
}

.lead {
    margin-left: 50px;
}

.deecampimg {
    width: 100%;
    height: 40%;
    text-align:left;
    border: none;
    border-collapse: collapse;
    margin-bottom: auto;

}

.media {
    margin-top: 50px;
    line-height: 1.7;
    margin-left: 0px;
    margin-right: 0px;
}
.media h2 {
    margin-left: 20px;
    margin-bottom: 50px;
}
.media div {
    margin-left: 30px;
    margin-right: 0px;
    line-height: 2.3;
}

.media img {
    width: auto;
    max-width: 390px;
    min-width: 50px;
    height: auto;
    max-height: 500px;
    min-height: 70px;
}

.test_button {
    width: 20%;
    height: 20%;
    color:lightblue;
}

.testbuttontext {
    color: whitesmoke;
}
